*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    margin: 0;
}

.page {
    overflow: hidden;

    &.scroll-hide {
        height: calc(var(--vh, 1vh) * 100);
    }
}

.container {
    width: 100%;
    margin: 0 auto;
    padding: 0 10px;
    box-sizing: border-box;

    @media (min-width: 576px) {
        max-width: 540px;
    }

    @media (min-width: 768px) {
        max-width: 758px;
    }

    @media (min-width: 992px) {
        max-width: 940px;
    }

    @media (min-width: 1200px) {
        max-width: 1100px;
    }

    @media (min-width: 1400px) {
        max-width: 1120px;
    }
}

.new-header {
    width: 100vw;
    padding: 17px 0 18px;
    background: #FFFFFF;

    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;

    @media(max-width:767px) {
        padding: 11px 0 13px;
    }

    &.active {
        border-bottom: 1px solid #f0f0f0;
    }
}

.new-header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.new-header__logo {
    display: block;
    width: 124.92px;
    height: 36px;

    img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.new-nav {
    @media(max-width:767px) {
        position: absolute;
        top: 60px;
        right: 0;

        background: rgba(255, 242, 241, 0.94);
        backdrop-filter: blur(13px);

        width: 100vw;
        height: calc(var(--vh, 1vh) * 100 - 60px);
        padding: 62px 0 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;

        opacity: 0;
        visibility: hidden;
        transition: opacity .3s linear;
        overflow-y: scroll;
        box-sizing: border-box;

        &::-webkit-scrollbar {
            display: none;
        }

        -ms-overflow-style: none;
        /* IE and Edge */
        scrollbar-width: none;
        /* Firefox */

        &.show {
            opacity: 1;
            visibility: visible;
        }
    }
}

.new-nav__btn {
    margin-bottom: 77px;

    @media(min-width:768px) {
        display: none !important;
    }
}

.new-nav__social {
    display: none;

    @media(max-width:767px) {
        display: block;
    }
}

.new-nav__social-title {
    margin: 0;
    margin-bottom: 23px;

    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    text-align: center;
    color: #141414;
}

.new-nav__social-list {
    display: flex;
    align-items: center;
    justify-content: center;
}

.new-nav__social-link {
    display: block;
    width: 30px;
    height: 30px;

    img {
        width: 30px;
        height: 30px;
    }

    &+& {
        margin-left: 33px;
    }
}

.new-nav__list {
    display: flex;
    align-items: center;
    padding: 0;
    margin: 0;

    @media(max-width:767px) {
        flex-direction: column;
        margin-bottom: 41px;
    }
}

.new-nav__item {
    list-style: none;

    &+& {
        margin-left: 30px;

        @media(max-width:767px) {
            margin-left: 0;
            margin-top: 21px;
        }
    }
}

.new-nav__link {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 20px;
    color: #0A0A0A;
    text-decoration: none;

    transition: color .2s linear;

    @media(min-width:1024px) {
        &:hover {
            color: #eb5757;
        }
    }

    @media(max-width:767px) {
        font-weight: 400;
        font-size: 18px;
        line-height: 27px;
    }
}

.new-header__btn {
    padding: 10px 25px 9px;
    background: #F04E3D;
    border-radius: 46px;

    transition: .2s linear;

    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 21px;
    text-align: center;
    color: #FFFFFF;
    text-decoration: none;

    @media(min-width:1024px) {
        &:hover {
            background: darken(#F04E3D, 10%);
        }
    }

    @media(max-width:767px) {
        display: none;
    }
}

.new-header__show-menu {
    display: none;

    @media(max-width:767px) {
        display: flex;
        align-items: center;

        border: none;
        background: transparent;

        font-family: 'Poppins';
        font-style: normal;
        font-weight: 500;
        font-size: 13px;
        line-height: 20px;
        color: #0A0A0A;

        img {
            margin-right: 4px;
        }

        &.hide {
            display: none;
        }
    }
}

.new-header__close-menu {
    display: none;
    padding: 0;
    width: 20px;
    height: 20px;

    @media(max-width:767px) {
        border: none;
        background: transparent;

        &.show {
            display: block;
        }
    }
}

