            width: 371px;
            height: 442px;
            left: 497px;
            top: 94px;
            border-radius: 32px 0px 0px 32px;
        }

        @media(max-width:767px) {
            top: unset;
            bottom: -50px;
            left: -10px;
            right: -10px;

            border-radius: 0;
            width: unset;
            height: 270.42px;
            z-index: -1;
        }
    }

    @media(max-width:991px) {
        padding-top: 166px;
    }

    @media(max-width:767px) {
        padding-top: 90px;
    }
}

.intro-sec__title {
