            line-height: 21px;
        }
    }

    @media(min-width:1024px) {
        &:hover {
            background: darken(#F04E3D, 10%);
        }
    }

    @media(max-width:767px) {
        margin-left: auto;
        margin-right: auto;
        padding: 16px 23px;

        img {
            width: 22px;
            height: 22px;
        }
    }
}

.intro-sec__video {
    width: 876px;
    height: 545px;

    box-shadow: 0px 11.6169px 23.2338px rgba(0, 0, 0, 0.15);
    border-radius: 6.7871px;
    overflow: hidden;

    position: absolute;
    top: 137px;
    right: -357px;

    video {
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    @media(max-width:1439px) {
        width: 776px;
        height: 466px;
        right: -277px;
    }

    @media(max-width:991px) {
        width: 581px;
        height: 361.47px;
        left: 400px;
        right: unset;
        top: 128px;
    }

    @media(max-width:767px) {
        width: 290px;
        height: 180.42px;
        margin: 75px auto 0;
        position: static;
    }
}

