    }

    @media(max-width:991px) {
        font-size: 35px;
        line-height: 48px;
        max-width: 576px;
        margin-bottom: 90px;
    }

    @media(max-width:767px) {
        font-size: 25px;
        line-height: 38px;
        text-align: start;
        margin-bottom: 29px;
        max-width: 290px;
    }
}

.features-sec__list {
    margin-bottom: 68px;

    @media(max-width:767px) {
        margin-bottom: 140px;
    }
}

.features-sec__item {
    display: flex;
    flex-direction: column;

    min-height: 600px;
    position: relative;

    @media(max-width:991px) {
        min-height: 340px;
    }

    &::before {
        content: '';
        width: 50vw;
        height: 600px;

        border-radius: 40px 0px 0px 40px;
        position: absolute;
        top: -40px;
        left: 650px;

        @media(max-width:1439px) {
            left: 630px;
            height: 520px;
        }

        @media(max-width:1199px) {
            left: 560px;
        }

        @media(max-width:991px) {
            // width: 363px;
            height: 340px;
            left: 427px;
            top: -51px;
        }

        @media(max-width:767px) {
            height: 284.52px;
            left: -10px;
            right: -10px;
            width: unset;
            top: unset;
            bottom: -50px;
            z-index: -1;
            border-radius: 0;
        }
    }

    &.blue::before {
        background: #EAFAFB;
    }

    &.lavender::before {
        background: #F4F1FA;
    }

    &.beige::before {
        background: #FCF2F0;
    }

    &:nth-child(even) {
        align-items: flex-end;

        .features-sec__img {
            left: unset;
            right: 530px;

            @media(max-width:1439px) {
                right: 530px;
            }

            @media(max-width:1399px) {
                right: 520px;
            }

            @media(max-width:1199px) {
                right: 442px;
            }

            @media(max-width:991px) {
                right: 358px;
            }

            @media(max-width:802px) {
                right: 355px;
            }
        }

        &::before {
            border-radius: 0px 40px 40px 0px;
            right: 650px;
            left: unset;

            @media(max-width:1439px) {
                right: 630px;
            }

            @media(max-width:1199px) {
                right: 560px;
            }

            @media(max-width:991px) {
                right: 427px;
            }

            @media(max-width:767px) {
                right: -10px;
                left: -10px;
                border-radius: 0;
            }
        }
    }

    &+& {
        margin-top: 50px;

        @media(max-width:991px) {
            margin-top: 99px;
        }
    }
}

